import React, {Component} from 'react';
import Icon from '../assets/Icon';
import './css/information-products.css';

class InformationProduct extends Component{
  render(){
    return(
      <div className="wrap">
        <Icon iconName="fas fa-window-close" id="close-modal" onClick={() => this.props.showModal(false)} />
        <div className="information-product">
          <figure>
            <img loading="lazy" src={`${process.env.PUBLIC_URL}/img/mobiles/zte/zte-blade-vantage.png`} alt={'mayita'} title={'mayita'} />
          </figure>
          <h3>Modelo:<span>&nbsp;&nbsp;Zte Blade Vantage</span></h3>
          <h3>Marca:<span>&nbsp;&nbsp;Zte</span></h3>
          <h3>Memoria RAM:<span>&nbsp;&nbsp;8 GB</span></h3>
          <h3>Batería:&nbsp;&nbsp;<span>128</span>&nbsp;<span>mAh</span></h3>
          <h3>Camara Frontal:&nbsp;&nbsp;<span>5</span>&nbsp;<span>MP</span></h3>
          <h3>Resolución:<span>&nbsp;&nbsp;1920 x 1080</span></h3>
          <h3>Precio:&nbsp;&nbsp;<span>$/.</span>&nbsp;<span>850</span></h3>
        </div>
      </div>
    )
  }
}

export default InformationProduct;