import React, { Component } from 'react';
import Icon from '../assets/Icon';
import './css/product.css';
import Swal from 'sweetalert2';

class Product extends Component{
  state = {
    stock: Number(this.props.stock),
    prices: Number(this.props.stock)
  }

  increase = () =>{
    if (this.state.stock !== this.state.prices){
      this.setState({
        stock: this.state.stock + 1
      })
    }
  }

  decrease = () =>{
    if (this.state.stock !== 0){
      this.setState({
        stock: this.state.stock - 1
      })
    }
  }

  messageForAddedProduct = stock =>{
    console.log(stock)
    if (stock === 0 ){
      Swal.fire({
        showConfirmButton: false,
        padding: '10px 0',
        background: 'rgba(255,255,255,0.8)',
        html:
        '<span id="danger" class="alert">Se produjo un error al añadir el producto al carrito <i class="fas fa-window-close"></i></span>'
      })
    } else {
      Swal.fire({
        showConfirmButton: false,
        padding: '10px 0',
        background: 'rgba(255,255,255,0.8)',
        html:
        '<span id="check" class="alert">Se añadido el producto correctamente al carrito <i class="fas fa-check-circle"></i></span>'
      })
    }
  }

  render(){
    const { id, imagen, modelo, precio, stock } = this.props;
    return (
      <div className="product" key={id}>
        <figure onClick={() => this.props.showModal(true)}>
          <img loading="lazy" src={`${process.env.PUBLIC_URL}/${imagen}`} alt={modelo} title={modelo} />
        </figure>
        <h3>{modelo}</h3>
        <h4>$/. {precio}</h4>

        <div id="quantity">
          <button onClick={this.increase}>
            <Icon iconName="fas fa-plus" iconStyle={{padding: "0"}}/>
          </button>
          
          <div id="count">{this.state.stock}</div>
          
          <button onClick={this.decrease}>
            <Icon iconName="fas fa-minus" iconStyle={{padding: "0"}}/>
          </button>
        </div>

        <button className="add-to-cart" onClick={() => {
          this.messageForAddedProduct(this.state.stock);
          this.props.numberProducts();
          }}>
          <Icon iconName="fas fa-cart-plus" iconTitle="Añade un producto al Carrito" iconStyle={{padding: "0"}}/>&nbsp;&nbsp;Añadir al carrito</button>
      </div>
    )
  }
}

export default Product;